import { Population } from '../types/types'

// HighChartJS
interface Chart {
  categories: string[]
  series: {
    name: string
    data: number[]
  }
}

// Chart
export const changeFormatForChart = (population: Population[], prefName: string): Chart => ({
  categories: population.map((data) => String(data.year)),
  series: {
    name: prefName,
    data: population.map((data) => data.value),
  },
})
